  //   //--------------------------------------------- ESTATÍSTICAS ------------------------------------------------------//
  getStatistics(answer:number,correct:string,level:number,id_q:number){

    var st_answer = this.decodeQuestion(answer);
    var n_correct = this.decodeCorrect(correct);

    var name;
    var player;

    switch (this.vez) {
      case 1:
          player = this.play1;
          name = this.play1.nome;
        break;
      case 2:
          player = this.play2;
          name = this.play2.nome;
        break;
    }

    this.statistics = {
      jogo: 'bets',
      resposta_correta: correct,
      resposta: st_answer,
      assunto: 'Reações Orgânicas',
      sala: player.classRoom,
      id_pergunta: id_q,
      tempo_seg: 30 - this.regrecivTime,
      nivel: level,
      resposta_correta_num: n_correct,
      resposta_num: answer,
      dica: false,
      //desnecessário
      nome_player: player.nome
    }

    if(this.getdica)
      this.statistics.dica = true;

    // console.log(correct);
    // console.log(answer);
    // console.log(this.arrAlter);
    this.save();
  }

  decodeQuestion(answer){
    switch (answer) {
      case 1:
          return 'a';
      case 2:          
          return 'b';
      case 3:          
          return 'c';
      case 4:          
          return 'd';
      case 5:          
          return 'e';
    }
  }

  decodeCorrect(correct){
    switch (correct) {
      case 'a':
          return 1;
      case 'b':
          return 2;
      case 'c':
          return 3;
      case 'd':
          return 4;
      case 'e':
          return 5;
    }
  }

  save(){
        this.http.builder('statistics')
            .insert(this.statistics)
            .then((res) => {
                console.log("salvo");
            })
    }
