import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { platformBrowserDynamic } from "@angular/platform-browser-dynamic";

//import { NgvasModule, tweens, hitAreas } from "ngvas";

import { AppComponent } from './app.component';
import { RulesGameComponent } from './rules-game/rules-game.component';
import { RankingGameComponent } from './ranking-game/ranking-game.component';
import { CreditsGameComponent } from './credits-game/credits-game.component';
import { PlayGameComponent } from './play-game/play-game.component';
import { AppHttpService } from './app-http.service';
import { CardComponent } from './play-game/card/card.component';

//timer
import { SimpleTimer } from 'ng2-simple-timer';
import { CountTimerComponent } from './play-game/count-timer/count-timer.component';

const appRoutes:Routes = [
  {path: 'play', component: PlayGameComponent},
  {path: 'credits', component: CreditsGameComponent},
  {path: 'rules', component: RulesGameComponent},
  {path: 'ranking', component: RankingGameComponent},
]

@NgModule({
  declarations: [
    AppComponent,
    RulesGameComponent,
    RankingGameComponent,
    CreditsGameComponent,
    PlayGameComponent,
    CardComponent,
    CountTimerComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes)
    //NgvasModule
  ],
  providers: [
    AppHttpService,
    CardComponent,
    SimpleTimer
  ],  
  bootstrap: [
    AppComponent
    ]
})
export class AppModule { }



