import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/toPromise';

interface Options {
    limit?:number;
    page?:number;
}

interface RequestOptions {
    headers?:any;
}

@Injectable()
export class AppHttpService {
    private url: string;
    private options: RequestOptions = {};

    constructor (private http: Http) {}

    setAccessToken (token: string) {
        let header = new Headers({'Authorization': 'Bearer ' + token});
        this.options.headers = header;
    }

    //  http://200.19.181.126/org-laravel/index.php/api/
    client(url: string) {
        this.url = 'http://200.19.181.126/org-laravel/index.php/api/' + url;
        return this;
    }

    builder (resource: string) {
        this.url = 'http://200.19.181.126/org-laravel/index.php/api/' + resource;
        return this;
    }

    list (options: Options = {}) {
        let url = this.url;

        if (options.limit === undefined) {
            options.limit = 100;
        }

        if (options.page === undefined) {
            options.page = 1;
        }

        url += '?limit=' + options.limit;
        url += '&page=' + options.page;

        return this.http.get(url, this.options)
            .toPromise()
            .then((res) => {
                return res.json() || {};
            });
    }

    view (id: number) {
        return this.http.get(this.url + '/' + id, this.options)
            .toPromise()
            .then((res) => {
                return res.json() || {};
            });
    }


    social () {
        return this.http.get(this.url, this.options)
            .toPromise()
            .then((res) => {
                return res.json() || {};
            });
    }

    insert (data: Object) {
        return this.http.post(this.url, data, this.options)
            .toPromise()
            .then((res) => {
                return res.json() || {};
            });
    }
}



